"use client";

import React from "react";

interface CancelBookingModalProps {
  show: boolean;
  onConfirm: () => void;
  onDismiss: () => void;
}

const CancelBookingModal: React.FC<CancelBookingModalProps> = ({
  show,
  onConfirm,
  onDismiss,
}) => {
  if (!show) return null;

  return (
    <div className='fixed inset-0 flex justify-center items-center z-50'>
      {/* Background Overlay */}
      <div className='absolute inset-0 bg-black/60 backdrop-blur-sm'></div>

      {/* Modal Content */}
      <div className='relative bg-white dark:bg-gray-800 p-6 rounded-lg max-w-sm w-full space-y-4 z-50 shadow-lg mx-4'>
        <p className='text-lg font-semibold text-center text-gray-900 dark:text-white'>
          Are you sure you want to cancel?
        </p>
        <div className='flex flex-col gap-4'>
          <button
            className='px-4 py-2 rounded-md bg-red-500 text-white w-full'
            onClick={onConfirm}
          >
            Yes, Cancel
          </button>
          <button
            className='px-4 py-2 rounded-md bg-gray-300 dark:bg-gray-700 text-gray-900 dark:text-white w-full'
            onClick={onDismiss}
          >
            No, Go Back
          </button>
        </div>
      </div>
    </div>
  );
};

export default CancelBookingModal;
